.home-1 {
	position: relative;
	.swiper-container {
		.swiper-button-prev {
			background: none;
			display: flex;
			justify-content: center;
			align-items: center;
			border-radius: 50%;
			width: 50px;
			height: 50px;
			border: 2px solid color-rgba(color-get($mau, main), 0);
			@include tran(.2s);
			left: 25px;
			// +media-breakpoint-up(xl)
			// 	left: 75px
			&:before {
				@include fontawesome('\f104', $faf);
				color: white;
				font-size: r(40px); }
			&:hover {
				border-color: color-rgba(color-get($mau, main), 1);
				&:before {
					color: color-get($mau, main); } } }
		.swiper-button-next {
			background: none;
			display: flex;
			justify-content: center;
			align-items: center;
			border-radius: 50%;
			width: 50px;
			height: 50px;
			border: 2px solid color-rgba(color-get($mau, main), 0);
			@include tran(.2s);
			right: 25px;
			// +media-breakpoint-up(xl)
			// 	right: 75px
			&:before {
				@include fontawesome('\f105', $faf);
				color: white;
				font-size: r(40px); }
			&:hover {
				border-color: color-rgba(color-get($mau, main), 1);
				&:before {
					color: color-get($mau, main); } } }
		.swiper-slide {
			position: relative;
			.img {
				img {
					@include media-breakpoint-up(lg) {
						opacity: 0;
						visibility: hidden; } } }
			.content {
				position: absolute;
				width: 100%;
				height: 100%;
				top: 0;
				left: 0;
				display: flex;
				align-items: center;
				justify-content: center;
				h2 {
					color: white;
					font-weight: 400;
					display: none;
					@include media-breakpoint-up(sm) {
						display: block;
						font-size: r(30px); }
					@include media-breakpoint-up(lg) {
						font-size: r(60px); } }
				p {
					font-weight: 400;
					color: white;
					display: none;
					@include media-breakpoint-up(sm) {
						display: block;
						font-size: r(12px); }
					@include media-breakpoint-up(lg) {
						font-size: r(24px); } } } } }
	.button-next-section {
		position: absolute;
		left: 50%;
		bottom: r(28px);
		z-index: 1;
		transform: translateX(-50%);
		width: 50px;
		height: 50px;
		border-radius: 50%;
		border: 1px solid white;
		display: flex;
		justify-content: center;
		align-items: center;
		@include tran(.2s);
		cursor: pointer;
		@include media-breakpoint-down(xs) {
			display: none; }
		span {
			color: white;
			@include tran(.2s); }
		&:hover {
			border-color: color-get($mau, main);
			background: color-rgba(color-get($mau, main),.15);
			transform: translateX(-50%) translateY(-10px);
			box-shadow: 0 2px .2rem color-get($mau, main);
			span {
				color: white; } } } }
