.promotion-list {
	padding: r(60px) 0 0 0;
	.item {
		text-decoration: none;
		display: block;
		margin-bottom: 30px;
		figure {

			@include tran(.2s);
			.img-box {
				overflow: hidden;
				img {
					object-fit: contain; } }
			figcaption {
				padding: 1rem r(20px) 1.5rem r(20px);
				h4 {
					font-size: r(18px);
					font-weight: 700;
					color: lighten(black,20%);
					margin-bottom: .25rem;
					@include tran(.2s); }
				p {
					font-size: r(15px);
					font-weight: 400;
					color: lighten(black, 40%);
					font-family: $arimo; } } }
		&:hover {
			figure {
				box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.3);
				figcaption {
					h4 {
						color: color-get($mau, main); } } } } } }
