.store-level {
	padding-top: 30px;
	.tab-nav {
		display: flex;
		justify-content: center;
		list-style-type: none;
		li {
			a {
				padding: 0 r(20px);
				font-size: r(16px);
				color: lighten(black,40%);
				text-transform: uppercase;
				cursor: pointer; }
			&:hover {
				a {
					color: color-get($mau, main);
					font-weight: 400; } }
			&.active {
				a {
					font-weight: 700;
					color: lighten(black,20%);
					text-decoration: underline; } } } }
	.tab-content {
		padding: r(70px) 0; } }
