.vhs-footer {
	background: darken(white,5%);
	.info {
		padding-top: r(42px);
		padding-bottom: r(24px);
		h3 {
			font-size: r(24px);
			font-weight: 700;
			color: lighten(black,20%);
			margin-bottom: r(24px); }
		.address-info {
			p {
				font-family: $arimo;
				font-size: r(16px);
				a {
					color: lighten(black,20%); } }
			ul {
				list-style-type: none;
				font-family: $arimo;
				li {
					color: lighten(black,20%);
					font-size: r(16px);
					a {
						color: lighten(black,20%);
						text-decoration: none; } }
				@include media-breakpoint-up(lg) {
					li {
						display: inline-block; } } } }
		.address-bottom {
			margin-top: r(36px);
			p {
				font-family: $arimo;
				font-size: r(16px);
				color: lighten(black,20%);
				display: flex;
				align-items: center; }
			img {
				margin-left: 1rem;
				max-width: 100px; } } }
	.footer-right {
		display: flex;
		flex-direction: column;
		height: 100%;
		margin-top: 1.25rem;
		@include media-breakpoint-up(lg) {
			margin-top: 0; }
		.form {
			.subscribefrm {
				display: flex;
				flex-wrap: wrap;
				position: relative;
				input {
					height: 40px;
					width: 100%;
					color: #495057;
					background-color: #fff;
					border: 1px solid darken(white,40%); }
				button {
					width: 140px;
					border-top-right-radius: 4px;
					border-bottom-right-radius: 4px;
					background: lighten(black,40%);
					border: 0;
					height: 40px;
					position: absolute;
					cursor: pointer;
					top: 0;
					right: 0;
					color: white;
					@include tran(.2s);
					&:focus {
						box-shadow: none; }
					&:hover {
						background: color-get($mau, main); } } } }
		.social {
			height: 100%;
			padding: 1rem 0 0 0;
			text-align: center;
			@include media-breakpoint-up(lg) {
				padding-bottom: .5rem;
				display: flex;
				flex-direction: row;
				align-items: flex-end;
				justify-content: flex-end; }
			a {
				color: lighten(black, 40%);
				font-size: r(24px);
				+ a {
					margin-left: r(28px); }
				&:hover {
					color: color-get($mau, main);
					@include tran(.2s);
					text-decoration: none; } } } }
	.copyright {
		.copyright-wrap {
			border-top: 1px solid darken(white,40%);
			padding: 21px 0;
			display: flex;
			flex-direction: column;
			@include media-breakpoint-up(lg) {
				flex-direction: row;
				justify-content: space-between; }
			p {
				font-size: r(14px);
				color: darken(white,40%);
				text-align: center;
				a {
					color: darken(white,40%); } }
			ul {
				list-style-type: none;
				display: flex;
				justify-content: center;
				li {
					a {
						color: darken(white,40%);
						font-size: r(14px); }
					+ li {
						position: relative;
						margin-left: 11px;
						&:before {
							position: absolute;
							top: 50%;
							transform: translateY(-50%);
							left: -5px;
							content: '';
							height: 13px;
							width: 1px;
							background: darken(white,40%); } } } } } } }
