@mixin lgbt-linear {
	background-image: linear-gradient(to right, rgb(234,34,55) 0%, rgb(254,211,48) 15%, rgb(229,23,126) 33%, rgb(172,39,126) 49%, rgb(83,198,219) 67%, rgb(115,189,76) 84%, rgb(234,34,55) 100%); }

@mixin lgbt-radial {
	background-image: radial-gradient( rgb(172,39,126), rgb(115,189,76)); }

@mixin bg-grad1 {
	background: linear-gradient(312deg, rgba(115,189,76,1) 0%, rgba(0,99,99,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#006363', endColorstr='#73BD4C',GradientType=1 ); }


@mixin bg-grad2 {
	background: linear-gradient(180deg, color-rgba(black,0) 0%, color-rgba(black,.75) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#000000',GradientType=0 ); }

@mixin bg-grad3 {
	background: linear-gradient(180deg, color-rgba(black,0) 0%, color-rgba(color-get($mau,main),.75) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#000000',GradientType=0 ); }

$philo: 'Arima Madurai', cursive;
$arimo: 'Arimo', sans-serif;
