html, body {
	font-size: 15px;
	font-weight: 400;
	line-height: 1.5;
	width: 100%;
	font-family: $philo;
	// max-width: 1600px
	margin-right: auto;
	margin-left: auto;
	@include media-breakpoint-up(lg) {
		font-size: 16.5px; }
	@include media-breakpoint-up(xl) {
		font-size: 18px; }
	// &.vi-vn
	// 	font-family: $madurai
	// &.en-us
 }	// 	font-family: $philo
#goTopButton {
	position: fixed;
	z-index: 5;
	width: 35px;
	height: 35px;
	bottom: 10px;
	right: 15px;
	display: none;
	@include tran(.2s);
	opacity: 0;
	transform: translateY(10px);
	.goTop-wrap {
		border: 1px solid color-get($mau, main);
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		width: 100%;
		height: 100%;
		@include tran(.2s);
		span {
			color: color-get($mau, main);
			@include tran(.2s); } }
	@include media-breakpoint-up(lg) {
		bottom: 10px;
		right: 45px;
		width: 40px;
		height: 40px; }
	&.active {
		transform: translateY(0);
		opacity: 1;
		display: flex!important; }
	&:hover {
		.goTop-wrap {
			background: color-rgba(color-get($mau, main), .75); }
		span {
			color: white; } } }

.container {
	@media (min-width: 1440px) {
		max-width: 1390px; }
	@media (min-width: 1600px) {
		max-width: 1440px; } }

ul, li, p, h1, h2, h3, h4, h5, h6 {
	margin-bottom: 0;
	padding: 0; }

img {
	width: 100%; }

body {
	@include media-breakpoint-down(md) {
		&.active {
			overflow: hidden;
			padding-right: 17px; } } }
.main-title {
	font-size: r(36px);
	font-weight: 700;
	font-family: $philo;
	margin-bottom: 1rem;
	text-align: center; }


input, textarea, select {
	display: block;
	padding: .375rem .75rem;
	font-size: 1rem;
	line-height: 1.5;
	background-clip: padding-box;
	border-radius: 4px;
	border: 1px solid darken(white, 40%);
	outline: none;
	transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
	&:focus {
		box-shadow: 0 0 .25rem color-get($mau, main);
		border-color: color-get($mau, main);
		outline: color-get($mau, main); } }

.store-category-page {
	.store-nav {
		.store-menu-list {
			.nav-item {
				&:nth-child(2) {
					.nav-link {
						color: color-get($mau,main); } } } } } }

.product-detail-page {
	.Module-165 {
		display: none; }
	.Module-168 {
		display: none; } }

.newspager, .productpager {
	padding-top: r(30px);
	padding-bottom: r(60px); }
.modulepager {
	.pagination {
		display: flex;
		justify-content: center;
		list-style-type: none;
		li {
			width: r(40px);
			height: r(40px);
			border: 1px solid transparent;
			a,span {
				display: flex;
				font-size: r(15px);
				font-family: $arimo;
				display: flex;
				justify-content: center;
				align-items: center;
				text-decoration: none;
				height: 100%;
				width: 100%;
				color: darken(white,40%); }
			&.active, &:hover {
				border-color: color-get($mau, main);
				a,span {
					color: color-get($mau, main); } } } } }

.store-detail {
	.briefcontent-1 {
		display: none; } }

.category-list {
	.briefcontent-2 {
		display: none; } }


.Module-184 {
	position: fixed;
	right: 0;
	width: 50px;
	height: 50px;
	background: color-get($mau, main);
	z-index: 13;
	top: 100px;
	@include media-breakpoint-up(md) {
		top: 160px; }
	.ModuleContent {
		height: 100%;
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;
			height: 100%;
			color: white;
			font-size: r(20px); } } }


.search-page {
	padding: 2rem 0;
	.search-page-top {
		.searchcontrols {
			margin-bottom: 1rem;
			.form-inline {
				@include media-breakpoint-up(sm) {
					max-width: 510px; }
				.form-group {
					margin-bottom: 0;
					input {
						border: 1px solid darken(white,12%);
						border-radius: 4px;
						font-family: $arimo;
						height: 40px;
						font-size: r(14px);
						color: color-get($mau, main);
						&:focus {
							border-color: color-get($mau, main);
							box-shadow: 0 0 .25rem color-get($mau,main); } }
					&:last-child {
						padding-left: .5rem;
						input {
							width: 100px;
							color: white;
							background: color-get($mau, main); } }
					&:first-child {
						flex-grow: 1;
						width: auto;
						flex-basis: 0;
						input {
							width: 100%; } } } } } }
	.btn-reindex {
		margin-top: .5rem;
		margin-bottom: 1rem;
		@include media-breakpoint-up(sm) {
			max-width: 402.5px; }
		input {
			width: 100%;
			border: 1px solid darken(white,12%);
			border-radius: 4px;
			font-family: $arimo;
			color: white;
			background: color-get($mau, main);
			height: 40px;
			font-size: r(14px);
			&:focus {
				border-color: color-get($mau, main);
				box-shadow: 0 0 .25rem color-get($mau,main); } } }
	.searchresults {
		.modulepager:first-child {
			display: none; }
		.searchresultlist {
			.searchresult {
				@include tran(.4s);
				&:nth-child(odd) {
					background: lighten(color-rgba(color-get($mau, main),.1 ),5%); }
				&:nth-child(even) {
					background: darken(color-rgba(color-get($mau, main),.2 ),5%); }
				&:hover {
					transform: translateY(-5px);
					box-shadow: 0 0 .5px color-get($mau, main); }
				.NeatHtml {
					padding: .5rem .75rem 0 .75rem!important;
					h3 {
						a {
							color: darken(color-get($mau,main), 30%); } } } } }

		.modulepager:last-child {
			.pagination {
				display: flex;
				justify-content: center;
				list-style-type: none;
				li {
					width: r(40px);
					height: r(40px);
					border: 1px solid transparent;
					a,span {
						display: flex;
						font-size: r(15px);
						font-family: $arimo;
						display: flex;
						justify-content: center;
						align-items: center;
						text-decoration: none;
						height: 100%;
						width: 100%;
						color: darken(white,40%); }
					&.active, &:hover {
						border-color: color-get($mau, main);
						a,span {
							color: color-get($mau, main); } } } } } } }

.product-detail-page {
	.productdetailpager {
		display: none; } }



.o2o-page {
	main {
		.container {
			padding: r(60px) 0;
			.button-wrap {
				margin-top: 40px;
				text-align: center;
				.btn-o2o {
					border: 1px solid color-get($mau, main);
					text-transform: uppercase;
					color: color-get($mau, main);
					padding: .5rem 3rem;
					font-size: 20px;
					&:hover {
						background: color-get($mau, main);
						color: white; }
					&:focus {
						box-shadow: none; } } } } } }
