.page-banner {
	position: relative;
	.page-banner-title {
		position: relative;
		h2 {
			position: absolute;
			top: calc(50% - 1rem);
			left: 50%;
			transform: translate(-50%,-50%);
			z-index: 10;
			color: white;
			font-size: r(30px);
			font-weight: 700;
			color: white; }

		.swiper-container {
			background: black;
			.swiper-slide {
				opacity: .6;
				@include media-breakpoint-down(sm) {
					height: 190px;
					overflow: hidden;
					img {
						object-fit: cover;
						object-position: center;
						width: initial;
						height: inherit; } } } } }
	.breadcrumb-wrap {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 5;
		display: flex;
		justify-content: center;
		align-items: center;
		@include media-breakpoint-down(sm) {
			display: none; }
		.breadcrumb {
			display: flex;
			justify-content: center;
			list-style-type: none;
			margin-top: 1.25rem;
			margin-bottom: 0;
			padding-left: 0;
			li {
				a {
					color: white;
					font-family: $arimo;
					font-size: r(14px); }
				+ li {
					margin-left: .5rem;
					position: relative;
					&:before {
						content: '|';
						margin-right: .5rem;
						color: white; } } } } } }
