.store-category {
	padding-top: 1rem;
	.category-filter {
		display: flex;
		justify-content: center;
		.dropdown {
			font-family: $arimo;
			width: r(300px);
			box-shadow: 0px 5px 15px 0px rgba(19, 28, 35, 0.3);
			position: relative;
			.title {
				min-height: r(40px);
				background: color-get($mau, xanh-2);
				border-top-left-radius: 4px;
				border-top-right-radius: 4px;
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding: 0 20px;
				cursor: pointer;
				p {
					color: white;
					font-size: r(16px); }
				span {
					font-size: r(25px);
					color: color-get($mau,xanh-3); } }
			.content {
				display: none;
				position: absolute;
				left: 0;
				width: 100%;
				z-index: 1;
				box-shadow: 0px 5px 15px 0px rgba(19, 28, 35, 0.3);
				ul {
					list-style-type: none;
					background: white;
					padding: r(20px);
					max-height: 23rem;
					overflow: auto;
					@include media-breakpoint-up(lg) {
						max-height: initial; }
					li {
						+ li {
							margin-top: .5rem; }
						a {
							font-size: r(14px);
							color: lighten(black, 20%);
							display: block; }
						&:hover,&.active {
							a {
								color: color-get($mau, main);
								text-decoration: underline; } } } } } } }
	.category-list {
		padding-top: 30px;
		.item {
			text-decoration: none;
			display: block;
			margin-bottom: 30px;
			figure {
				@include tran(.2s);
				.img-box {
					border: 2px solid darken(white,8%);
					@include tran(.2s);
					img {
						object-fit: contain; } }
				figcaption {
					padding: 1rem 0;
					@include media-breakpoint-up(sm) {
						padding: 1rem; }
					@include tran(.2s);
					h4 {
						font-weight: 700;
						color: lighten(black,20%);
						@include tran(.2s);
						font-size: 1rem;
						@include media-breakpoint-up(sm) {
							font-size: r(20px); } }
					p {
						font-family: $arimo;
						font-size: r(15px);
						font-weight: 400;
						color: lighten(black,40%);
						@include tran(.2s);
						.mdi {
							margin-right: .125rem;
							@include tran(.2s); } } } }
			&:hover {
				figure {
					box-shadow: 0px 8px 14.55px 0.45px rgba(19, 28, 35, 0.3);
					.img-box {
						border-color: color-get($mau, main); }
					figcaption {
						background: color-get($mau, main);
						h4, p {
							color: white;
							.mdi {
								color: color-get($mau, xanh-3); } } } } } } } }
