.contact {
	padding: r(60px) 0;
	.form-title {
		h2 {
			font-size: r(42px);
			font-weight: 700;
			color: lighten(black,20%); }
		p {
			font-size: r(15px);
			font-weight: 400;
			color: lighten(black,40%);
			font-family: $arimo; } }
	.wrap-form {
		>div {
			@include media-breakpoint-up(sm) {
				display: flex;
				flex-wrap: wrap; }
			.frm-captcha {
				display: flex;
				flex-direction: row-reverse;
				flex-wrap: wrap;
				width: 100%;
				@include media-breakpoint-up(sm) {
					width: 65%; }
				.frm-captcha-input {
					flex-grow: 1;
					max-width: 100%;
					flex-basis: 0;
					label {
						display: none; }
					input {
						width: 100%;
						min-width: initial; } }
				.RadCaptcha_Default {
					width: initial!important;
					span {
						display: none; }
					img {
						width: 130px!important;
						height: 40px!important; }
					>div {
						>div {
							width: fit-content;
							display: flex;
							.rcRefreshImage {
								display: block;
								height: 40px;
								width: 40px;
								font-size: 0;
								&:before {
									content: '';
									background-image: url('/Data/Sites/1/skins/default/img/refresh_captcha.png');
									background-repeat: no-repeat;
									background-size: 75%;
									background-position: center;
									display: block;
									width: 40px;
									height: 40px; } } } } } }
			.form-wizard {
				display: flex;
				flex-wrap: wrap;
				margin: 0 -15px;
				.form-group {
					select {
						display: block;
						width: 100%;
						height: calc(2.25rem + 2px);
						padding: .375rem .75rem;
						font-size: 1rem;
						line-height: 1.5;
						background-clip: padding-box;
						transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out; }
					input,textarea,select {
						width: 100%;
						border: 1px solid darken(white,12%);
						border-radius: 4px;
						font-family: $arimo;
						color: darken(white,40%);
						font-size: r(13px);
						&:focus {
							border-color: color-get($mau, main); } }
					textarea {
						min-height: r(150px); }
					select,input {
						min-height: r(50px); }
					label {
						text-transform: uppercase;
						font-family: $arimo;
						font-size: r(13px);
						font-weight: 700;
						color: lighten(black,20%); } } }
			.frm-btnwrap {
				@include media-breakpoint-up(sm) {
					width: 35%;
					padding-left: 15px; }
				label {
					display: none; }
				.frm-btn {
					.frm-btn-reset {
						display: none; }
					.frm-btn-submit {
						background: color-get($mau, main);
						font-size: r(18px);
						font-weight: 400;
						color: white;
						width: 100%;
						display: flex;
						justify-content: center;
						align-items: center; } } } } } }
