// Colors
@function color-get($bn-colors, $co) {
    $mapcolor: map-get($bn-colors, "#{$co}");
    @if ($mapcolor) {
        @return $mapcolor; }
    @else {
        @return white; } }

@function color-rgba($co: #fff, $o:1) {
    @if ($co) {
        @return rgba(red($co),green($co),blue($co),$o); }
    @else {
        @return white; } }

// Set color lighten or darken
@function color-up($co, $amount:0) {
    $tmp: lighten($co, $amount);
    @return $tmp; }
@function color-down($co, $amount:0) {
    $tmp: darken($co, $amount);
    @return $tmp; }

@function r($size) {
	@return ($size/18px)*1rem; }

@mixin line($line) {
	display: -webkit-box;
	text-overflow: ellipsis;
	-webkit-line-clamp: $line;
	overflow: hidden;
	-webkit-box-orient: vertical; }

@mixin material($content) {
	display: inline-block;
	font-family: "Material Design Icons";
	font-size: inherit;
	text-rendering: auto;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	content: $content; }

@mixin linearicons($content) {
	font-family: 'Linearicons-Free';
	content: $content;
	speak: none;
	font-style: normal;
	font-weight: 400;
	font-variant: normal;
	text-transform: none; }

$faf: 'Font Awesome 5 Free';
$fab: 'Font Awesome 5 Brands';
@mixin fontawesome($content,$font) {
	font-family: $font;
	content: $content;
	display: inline-block;
	font-style: normal;
	font-variant: normal;
	text-rendering: auto;
	font-weight: 900; }

@mixin tran($time) {
	transition: all $time ease-in-out; }
