.store-brand {
	.brand-filter {
		ul {
			display: flex;
			flex-wrap: wrap;
			list-style-type: none;
			justify-content: center;
			font-family: $arimo;
			padding-top: 1.5rem;
			padding-bottom: 2rem;
			li {
				a {
					padding: 0 .5rem;
					color: darken(white, 50%);
					text-transform: uppercase;
					font-size: r(16px);
					text-decoration: none;
					&:hover {
						color: color-get($mau, main); } }
				&.active {
					a {
						color: black;
						font-weight: 700;
						text-decoration: underline; } } } } }
	.brand-list {
		padding-bottom: 20px;
		.item {
			margin-bottom: 10px;
			display: block;
			border: 2px solid darken(white,12%);
			@include tran(.2s);
			text-decoration: none;
			height: 100%;
			max-height: r(90px);
			height: r(90px);
			padding: 0 r(20px);
			.item-box {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				height: 100%;
				.title {
					max-width: 100%;
					flex: 0 0 100%;
					@include media-breakpoint-up(sm) {
						max-width: 66.6667%;
						flex: 0 0 66.6667%; }
					h4 {
						font-family: $philo;
						font-weight: 700;
						color: lighten(black,20%);
						@include tran(.4st);
						font-size: 1rem;
						@include media-breakpoint-up(sm) {
							font-size: r(20px); } } }
				.desc {
					max-width: 100%;
					flex: 0 0 100%;
					color: lighten(black, 40%);
					font-size: r(15px);
					@include media-breakpoint-up(sm) {
						max-width: 33.3333%;
						flex: 0 0 33.3333%; } } }
			&:hover {
				border-color: color-get($mau, xanh-1);
				box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.3);
				.item-box {
					.title {
						h4 {
							color: color-get($mau,xanh-1); } } } } } } }
