.home-2 {
	padding-top: 12px;
	article {
		overflow: hidden;
		.item-list {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			margin: 0 -6px; }
		.item {
			display: block;
			padding: 0 6px;
			flex: 0 0 100%;
			max-width: 100%;
			&:nth-child(1) {
				order: 1; }
			&:nth-child(2) {
				order: 4; }
			&:nth-child(3) {
				order: 3; }
			&:nth-child(4) {
				order: 2; }
			&:nth-child(5) {
				order: 5; }
			@media (min-width: 520px) {
				flex: 0 0 50%;
				max-width: 50%;
				&:last-child {
					margin: auto;
					// flex: 0 0 100%
 } }					// max-width: 100%
			@include media-breakpoint-up(lg) {

				flex: 0 0 33.3333%;
				max-width: 33.3333%;
				&:first-child, &:nth-child(4) {
					flex: 0 0 50%;
					max-width: 50%; }
				&:last-child {
					flex: 0 0 33.3333%;
					max-width: 33.3333%; } }
			figure {
				position: relative;
				margin-bottom: 12px;
				position: relative;
				// &:before
				// 	content: ''
				// 	position: absolute
				// 	top: 0
				// 	left: 0
				// 	width: 100%
				// 	height: 100%
				// 	opacity: 0
				// 	+tran(.2s)
				// 	background: color-get($mau, main)
				// 	z-index: 1
				.img-box {
					overflow: hidden;
					img {
						@include tran(.2s); } }
				figcaption {
					z-index: 2;
					position: absolute;
					width: 100%;
					left: 0;
					bottom: 0;
					@include bg-grad2;
					transition: background .4s ease-in-out;
					padding: r(16px);
					@include media-breakpoint-up(lg) {
						padding: r(32px); }
					h4 {
						font-size: r(24px);
						font-weight: 700;
						color: white;
						text-align: center;
						@include media-breakpoint-up(lg) {
							font-size: r(30px); }
						@include media-breakpoint-up(xl) {
							font-size: r(36px); } } } }
			&:hover {
				figure {
					// &:before
					// 	opacity: .25
					.img-box {
						img {
							transform: scale(1.1); } }
					figcaption {} } } } } }
