.vhs-header {
	position: sticky;
	top: 0;
	z-index: 20;
	margin-bottom: 5px;
	background: white;
	font-family: $arimo;
	&:after {
		content: '';
		height: 5px;
		width: 100%;
		position: absolute;
		left: 0;
		top: 100%;
		@include lgbt-linear; }
	.container {
		@media (min-width: 1440px) {
			padding: 0 8%; }
		.language {
			margin-left: auto;
			display: flex;
			border: 1px solid #d7d7d7;
			border-radius: 4px;
			padding: 6px;
			align-items: center;
			cursor: pointer;
			position: relative;
			box-sizing: border-box;
			.current-lang {
				margin-right: 7px;
				.img {
					display: flex;
					align-items: cener; } }
			ul {
				list-style-type: none;
				display: none;
				position: absolute;
				width: 100%;
				top: 100%;
				left: 0;
				background: white;
				border: 1px solid #d7d7d7;
				background: white;
				box-sizing: border-box;
				z-index: 11;
				li {
					display: flex;
					align-items: center;
					justify-content: center; } }
			span {
				font-size: r(12px); } } }
	@include media-breakpoint-up(lg) {
		.logo {
			display: flex;
			height: 60px;
			align-items: flex-end;
			max-width: 160px;
			transition: all .5s ease-in-out;
			@include media-breakpoint-up(xl) {
				max-width: 200px;
				height: 70px; }
			@media (min-width: 1440px) {
				max-width: 240px;
				height: 80px; }
			.img {
				position: relative;
				img {
					width: 100%;
					&.mobile {
						display: none; }
					&.desktop {
						display: block; } }
				&:after {
					content: '';
					position: absolute;
					width: 100%;
					height: 20px;
					top: 100%;
					left: 0;
					z-index: 10;
					background-image: url('../img/logo_bottom.png');
					background-position: top center;
					background-size: 100%;
					background-repeat: no-repeat; } } }
		.menu-toggle {
			display: none; }
		.main-nav {
			width: 70%;
			ul {
				display: flex;
				list-style-type: none;
				padding-left: 0;
				margin-bottom: 0;
				height: 100%;
				align-items: center;
				li {
					a {
						font-size: r(15px);
						color: #333333;
						font-weight: 700;
						@include tran(.3s);
						&:hover {
							color: color-get($mau, main);
							text-decoration: none; } }
					+ li {
						margin-left: 1rem;
						@include media-breakpoint-up(xl) {
							margin-left: 1.5rem; }
						@media (min-width: 1440px) {
							margin-left: 30px; } }
					&.active {
						a {
							color: color-get($mau, main); } } } } }
		.search {
			position: relative;
			padding: 0 11px;
			span {
				font-size: r(16px);
				color: color-get($mau, main);
				cursor: pointer; }
			.Module {
				// display: none
				position: absolute;
				@include lgbt-radial;
				right: 0;
				top: 100%;
				z-index: 12;
				box-shadow: 0 0 .2rem color-get($mau, main);
				padding: 5px;
				opacity: 0;
				visibility: hidden;
				transform: translateY(10px);
				@include tran(.2s);
				&.active {
					opacity: 1;
					visibility: visible;
					transform: translateY(0); }
				.searchbox {
					position: relative;
					display: flex;
					input {
						font-size: r(16px);
						&:focus {
							outline: none; } }
					button {
						font-size: 0;
						position: absolute;
						top: 0;
						height: 100%;
						right: 0;
						background: none;
						border: 0;
						outline: none;
						z-index: 13;
						&:after {
							content: '';
							@include fontawesome('\f002', $faf);
							font-size: r(14px); } } } } }
		.backdrop {
			display: none; } }
	@include media-breakpoint-down(md) {
		.backdrop {
			position: fixed;
			top: 65px;
			right: 0;
			width: 100%;
			height: calc(100% - 65px);
			background: rgba(0,0,0,.85);
			display: none;
			z-index: 13; }
		.logo {
			.img {
				height: 60px;
				display: flex;
				align-items: center;
				justify-content: center;
				img {
					height: 50px;
					width: initial;
					&.desktop {
						display: none; }
					&.mobile {
						display: block; } } } }
		.search {
			margin-top: 20px;
			span {
				display: none; }
			.Module {
				.searchbox {
					position: relative;
					button {
						font-size: 0;
						position: absolute;
						top: 0;
						height: 100%;
						right: 7px;
						background: white;
						border: 0;
						outline: none;
						z-index: 13;
						border-radius: .25rem;
						&:after {
							content: '';
							@include fontawesome('\f002', $faf);
							font-size: r(14px);
							color: color-get($mau, main); } }
					input {
						display: block;
						width: 100%;
						height: calc(2.25rem + 2px);
						padding: .375rem .75rem;
						font-size: 1rem;
						line-height: 1.5;
						color: #495057;
						background-color: #fff;
						background-clip: padding-box;
						border: 0;
						border-radius: .25rem;
						&:focus {
							outline: 0; } } } } }
		.menu-toggle {
			width: 26px;
			height: 26px;
			margin-left: 1rem;
			display: flex;
			justify-content: center;
			align-items: center;
			border-radius: 4px;
			overflow: hidden;
			span {
				line-height: 26px;
				padding: 0 6px;
				color: color-get($mau, main);
				@include tran(.2s); }
			&.active {
				span {
					background: color-get($mau, main);
					color: white; } } }
		.main-nav {
			padding: 3rem 15px;
			position: fixed;
			top: 65px;
			left: 0;
			width: 100%;
			height: calc(100% - 65px);
			@include bg-grad1;
			@include tran(.2s);
			transform: translateX(-100%);
			max-width: 50%;
			z-index: 14;
			@include media-breakpoint-down(xs) {
				max-width: 100%; }
			&.active {
				transform: translateX(0); }
			ul {
				list-style-type: none;
				li {
					a {
						padding: 10px 0;
						display: block;
						font-size: 1rem;
						color: white;
						font-weight: 700; } } } } } }
